import './Header.scss';

interface IHeader {
	onSettingsClick: () => void;
	onDiagramsClick: () => void;
}

const Header = (i: IHeader) => {
	return (
		<div className="header">
			<div className="section">
				<img src="/icon1080.png" alt="" className="logo" />
				<div className="name">Matma</div>
			</div>
			<div className="section">
				<div className="widget" onClick={i.onDiagramsClick}>
					<img src="/svgs/diagram.svg" className="settings" />
				</div>
				<div className="widget" onClick={i.onSettingsClick}>
					<img src="/svgs/gear.svg" className="settings" />
				</div>
			</div>
		</div>
	);
};

export default Header;
