import './HomePage.scss';

const items = [
	{
		icon: '+',
		title: 'Dodawanie',
		subtitle:
			'Proste dodawanie, wszystkie opcje można zmienić w ustawieniach!',
		pageID: 1,
	},
	{
		icon: '-',
		title: 'Odejmowanie',
		subtitle: 'Odejmowanie w podanym zakresie',
		pageID: 2,
	},
	{
		icon: 'x',
		title: 'Mnożenie',
		subtitle: 'Mnożenie w konkretnym zakresie, łatwym do opanowania',
		pageID: 3,
	},
	{
		icon: '÷',
		title: 'Dzielenie',
		subtitle: 'Dzielenie liczb, które można ograniczyć czasowo',
		pageID: 4,
	},
];

const HomePage = () => {
	const navigate = (page: number) => {
		window.location.href =
			window.location.href.split('?')[0] + '?page=' + page;
	};
	return (
		<div className="home-page">
			<div className="items">
				{items.map((e, i) => (
					<div
						className="item"
						key={i}
						onClick={() => navigate(e.pageID)}
					>
						<div className="icon">{e.icon}</div>
						<div className="info">
							<div className="title">{e.title}</div>
							<div className="subtitle">{e.subtitle}</div>
						</div>
					</div>
				))}
			</div>
		</div>
	);
};

export default HomePage;
