import { useEffect, useState } from 'react';
import './QuestPage.scss';
import Popup from '../../Popup/Popup';
import { title } from '../../../../utils/StringUtils';
import * as Settings from '../../../../utils/Settings';

interface IQuest {
	symbol: string;
	page: number;
	questName: string;
	display: string;
}

const QuestPage = (i: IQuest) => {
	const [phase, setPhase] = useState(0);
	const [s, setSettings] = useState(null as any as Settings.Settings);
	const [sp, setSettingsProfile] = useState(
		null as any as Settings.EntrySetting
	);
	const [challenge, setChallenge] = useState([0] as number[]);
	const [stats, setStats] = useState({ correct: 0, avgTime: 0 });
	const [numField, setNumField] = useState(0 as any);
	const [questStart, setQuestStart] = useState(-1);
	const [timeout, setTout] = useState(null as any as NodeJS.Timeout);
	useEffect(() => {
		Settings.load();
		setSettings(Settings.get());
	}, []);
	useEffect(() => {
		if (s == null) return;
		setSettingsProfile(
			s.shareSameSettings ? s.settingList[0] : s.settingList[i.page - 1]
		);
	}, [s]);
	const nextChallenge = () => {
		if (challenge[3] !== undefined) {
			console.log(challenge[3]);

			const time = questStart != -1 ? Date.now() - questStart : 1;
			const correct = parseInt(numField) === challenge[3];
			setStats((p) => {
				return {
					...p,
					correct: p.correct + (correct ? 1 : 0),
					avgTime:
						p.avgTime === 0
							? time
							: (p.avgTime * challenge[0] + time) /
							  (challenge[0] + 1),
				};
			});
			if (!correct) {
				Settings.addIncorrect(challenge[1] + i.symbol + challenge[2]);
				console.log('Saved incorrect answer');
			}
		}
		if (challenge[0] === sp.totalQuestions) {
			console.log('finish');
			return;
		}
		if (timeout != null) {
			clearTimeout(timeout);
		}

		const lim =
			i.page === 3 || i.page === 4 ? bn(sp.maxRange) : sp.maxRange;
		let result = -1;
		let nums: number[] = [];
		while (
			result > sp.maxRange ||
			result === -1 ||
			(i.page === 4 && nums.find((e) => e === 0) != null)
		) {
			nums = [
				Math.floor(Math.random() * (sp.negativeNumbers ? 2 : 1) * lim) -
					(sp.negativeNumbers ? lim : 0),
				Math.floor(Math.random() * (sp.negativeNumbers ? 2 : 1) * lim) -
					(sp.negativeNumbers ? lim : 0),
			];
			nums.sort((a, b) => b - a);

			result = eval(
				nums[0] + (i.symbol === '/' ? '*' : i.symbol) + nums[1]
			);

			if (i.symbol === '/') [nums[0], result] = [result, nums[0]] as any;
		}
		setChallenge((c) => {
			return [c[0] + 1, ...nums, result];
		});
		setQuestStart(Date.now());
		setTout(
			setTimeout(() => {
				if (challenge[0] + 1 >= sp.totalQuestions) {
					setPhase((p) => p + 1);
					clearTimeout(timeout);
				} else next();
			}, sp.time * 1000)
		);
	};
	const begin = () => {
		setStats({ correct: 0, avgTime: 0 });
		nextChallenge();
		setPhase(1);
	};
	const next = () => {
		if (challenge[0] >= sp.totalQuestions) {
			nextChallenge();
			setPhase((p) => p + 1);
			clearTimeout(timeout);
			console.log('end');
		} else {
			nextChallenge();
			setNumField(0);
			setPhase(100);
			setTimeout(() => {
				setPhase(1);
			}, 1);
		}
	};
	const updateField = (ev: React.ChangeEvent<HTMLInputElement>) => {
		setNumField(ev.target.value.replace(/[a-zA-Z\s]*/gm, ''));
	};
	const navHome = () => {
		window.location.href = window.location.href.split('?')[0];
	};
	const restart = () => {
		setNumField(0);
		setStats({ correct: 0, avgTime: 0 });
		setChallenge([0]);
		setPhase(0);
	};
	return (
		<>
			{s != null && sp != null && (
				<div className="quest-page">
					{phase === 0 && (
						<Popup>
							<h1>{title(i.questName)}: test</h1>
							<h3 style={{ textAlign: 'center' }}>
								{sp.totalQuestions} pytań
								<br />
								{sp.time}s/pytanie
							</h3>
							<button onClick={begin}>Rozpocznij</button>
						</Popup>
					)}
					{phase === 1 && (
						<Popup className="challenge">
							<div
								className="bar bar-reducing"
								style={{ '--t': sp.time + 's' } as any}
							></div>
							<h1>
								{challenge[0]}/{sp.totalQuestions}
							</h1>
							<div className="inside">
								<h2>
									{challenge[1] +
										i.display +
										challenge[2] +
										' ='}
								</h2>
								<input
									type="number"
									className="guess"
									value={numField}
									onChange={updateField}
								/>
							</div>
							<button onClick={next}>
								{challenge[0] >= sp.totalQuestions
									? 'Zakończ'
									: 'Nastepny'}
							</button>
						</Popup>
					)}
					{phase === 2 && (
						<Popup className="result">
							<h1>Twój wynik:</h1>
							<h3>
								{stats.correct}/{sp.totalQuestions}
								<br />
								{(
									(stats.correct / sp.totalQuestions) *
									100
								).toFixed(2)}
								%
							</h3>
							<h3>
								{(stats.avgTime / 1000).toFixed(2)}s / pytanie
							</h3>
							<div
								className="bar"
								style={
									{
										'--p':
											(stats.correct /
												sp.totalQuestions) *
												100 +
											'%',
									} as any
								}
							></div>
							<div className="inside">
								<button onClick={restart}>Powtórz</button>
								<button onClick={navHome}>Powrót</button>
							</div>
						</Popup>
					)}
				</div>
			)}
		</>
	);
};

function bn(n: number) {
	let lastn = 1,
		currn = 2;
	while (!(lastn ** 2 <= n && currn ** 2 > n)) {
		currn++;
		lastn++;
	}
	return lastn;
}

export default QuestPage;
