export const KEY = 'kefir-dev:math-settings';

let settings: Settings = {
	shareSameSettings: true,
	settingList: [],
	incorrectList: [],
};
for (let i = 0; i < 4; i++)
	settings.settingList[i] = {
		time: 10,
		totalQuestions: 20,
		maxRange: 100,
		negativeNumbers: false,
	};

export const load = () => {
	let val: any = localStorage.getItem(KEY);
	if (val == null) {
		save();
	} else {
		val = JSON.parse(val) as object;
		settings = { ...settings, ...val };
	}
};
export const get = () => {
	return settings;
};
export const save = (sets: any = settings) => {
	localStorage.setItem(KEY, JSON.stringify(sets));
};
export const addIncorrect = (equation: string) => {
	let failed = 1;
	const exists = settings.incorrectList.find((e) => e[0] === equation);
	if (exists != null) {
		failed += exists[1];
		settings.incorrectList[settings.incorrectList.indexOf(exists)] = [
			equation,
			failed,
		];
	} else settings.incorrectList.push([equation, failed]);
	save();
};

export type Settings = {
	shareSameSettings: boolean;
	settingList: EntrySetting[];
	incorrectList: any[]; // [[N1+N2=N3, FAIL_COUNT]]
};
export type EntrySetting = {
	time: number;
	totalQuestions: number;
	maxRange: number;
	negativeNumbers: boolean;
};
