import { useEffect, useState } from 'react';
import './Checkbox.scss';

interface ICheckbox {
	checked?: boolean;
	onChange?: (b: boolean) => void;
}

const Checkbox = (i: ICheckbox) => {
	const [checked, setChecked] = useState(false);
	useEffect(() => {
		if (i.checked) setChecked(i.checked ? i.checked : false);
	}, [i.checked]);
	const toggle = () => {
		setChecked((p) => {
			if (i.onChange) i.onChange(!p);
			return !p;
		});
	};
	return (
		<div className="checkbox" onClick={toggle}>
			{checked && (
				<div className="checked">
					<img src="/svgs/check.svg" />
				</div>
			)}
			{!checked && (
				<div className="non-checked">
					<img src="/svgs/x.svg" />
				</div>
			)}
		</div>
	);
};

export default Checkbox;
