import { useEffect, useState } from 'react';
import * as Settings from '../../../../utils/Settings';
import './Diagrams.scss';
import Selector from '../../Selector/Selector';

interface IDiagrams {
	onClose: () => void;
}

const Diagrams = (i: IDiagrams) => {
	const symbols = ['', '+', '-', '*', '/'];
	const [problems, setProblems] = useState([] as any[]);
	const [search, setSearch] = useState(0);

	useEffect(() => {
		Settings.load();
		let list = Settings.get().incorrectList.sort((a, b) => b[1] - a[1]);
		setProblems(list);
	}, []);

	return (
		<div className="diagrams">
			<div className="top-bar">
				<h1>Błędne odpowiedzi</h1>
				<div className="widget" onClick={() => i.onClose()}>
					<img src="/svgs/x.svg" alt="x" />
				</div>
			</div>
			<Selector addAllOption={true} onChange={(n) => setSearch(n)} />
			<div className="list">
				{problems
					.filter((e) => e[0].includes(symbols[search]))
					.map((e, n) => (
						<div className="problem" key={n}>
							<div className="eq">{e[0]}</div>
							<div className="amt">{e[1]}</div>
						</div>
					))}
			</div>
		</div>
	);
};

export default Diagrams;
