import { useEffect, useState } from 'react';
import './Selector.scss';

interface ISelector {
	onChange?: (n: number) => void;
	addAllOption?: boolean;
}

const Selector = (i: ISelector) => {
	const [fields, setFields] = useState(
		'Dodawanie Odejmowanie Mnożenie Dzielenie'.split(' ')
	);
	const [selected, setSelected] = useState(0);

	const select = (n: number) => {
		setSelected(n);
		if (i.onChange) i.onChange(n);
	};
	useEffect(() => {
		if (i.addAllOption) {
			setFields((p) => {
				p.splice(0, p.length);
				return [
					...p,
					'Wszystkie',
					'Dodawanie',
					'Odejmowanie',
					'Mnożenie',
					'Dzielenie',
				];
			});
		}
	}, []);

	return (
		<div className="selector">
			{fields.map((e, n) => (
				<div
					className="field"
					key={n}
					{...{ pick: selected == n ? '' : null }}
					onClick={() => select(n)}
				>
					{e}
				</div>
			))}
		</div>
	);
};

export default Selector;
