import Popup from '../../Popup/Popup';
import * as Sets from '../../../../utils/Settings';
import { useEffect, useState } from 'react';
import Checkbox from '../../Checkbox/Checkbox';

import './Settings.scss';
import Selector from '../../Selector/Selector';

interface ISettings {
	onClose: () => void;
}

const entries = [
	{
		name: 'Dziel ustawienia',
		desc: 'Używaj tych samych ustawień dla każdego rodzaju zadań (zalecane)',
		setting: 'shareSameSettings',
		type: 'boolean',
	},
	{
		name: 'Czas (w sek)',
		desc: 'Maksymalny czas na pytanie w sekundach',
		setting: '%time',
		type: 'num',
	},
	{
		name: 'Liczba pytań',
		desc: 'Ilość pytań na jeden test',
		setting: '%totalQuestions',
		type: 'num',
	},
	{
		name: 'Zakres liczb',
		desc: 'Zakres, w jakim liczby mają być losowane',
		setting: '%maxRange',
		type: 'num',
	},
	{
		name: 'Ujemne liczby',
		desc: 'Czy w pytaniach mają pojawiać się ujemne liczby?',
		setting: '%negativeNumbers',
		type: 'boolean',
	},
];

const Settings = (i: ISettings) => {
	const [sets, setSets] = useState({} as Sets.Settings);
	const [selected, setSelected] = useState(0);
	const [entry, setEntry] = useState(null as any);

	const updateSelected = (n: number) => {
		setSelected(n);
		setEntry(null);
		setTimeout(() => {
			setEntry(sets.settingList[n]);
		}, 1);
	};

	const changed = (en: any, newValue: any) => {
		if (en.setting.charAt(0) == '%') {
			entry[en.setting.substring(1)] = newValue;
			sets.settingList[selected] = entry;
		} else {
			(sets as any)[en.setting] = newValue;
		}
		Sets.save(sets);
		Sets.load();
		setSets(Sets.get());

		setTimeout(() => {
			if (sets.shareSameSettings) {
				setSelected(0);
				setEntry(sets.settingList[0]);
			}
		}, 1);
	};

	useEffect(() => {
		Sets.load();
		setSets(Sets.get());
	}, []);
	useEffect(() => {
		if (sets == null || sets.settingList == null) return;
		updateSelected(0);
	}, [sets]);
	return (
		<Popup displClose onClose={() => i.onClose()} className="settings-page">
			<h1>Ustawienia</h1>
			<div className="list">
				{[entries[0]].map((e, i) => (
					<div className="setting" key={i}>
						<div className="info">
							<div className="name">{e.name}</div>
							<div className="desc">{e.desc}</div>
						</div>
						{e.type === 'boolean' && (
							<Checkbox
								checked={(sets as any)[e.setting]}
								onChange={(b) => changed(e, b)}
							/>
						)}
					</div>
				))}
				{!sets.shareSameSettings && (
					<Selector onChange={(n) => updateSelected(n)} />
				)}
				{entry &&
					entries
						.filter((e, i) => i != 0)
						.map((e, i) => (
							<div className="setting" key={i}>
								<div className="info">
									<div className="name">{e.name}</div>
									<div className="desc">{e.desc}</div>
								</div>
								{e.type === 'boolean' && (
									<Checkbox
										checked={entry[e.setting.substring(1)]}
										onChange={(b) => changed(e, b)}
									/>
								)}
								{e.type === 'num' && (
									<input
										className="num-input"
										type="number"
										onChange={(ev) =>
											changed(
												e,
												ev.currentTarget.valueAsNumber
											)
										}
										defaultValue={
											entry[e.setting.substring(1)]
										}
									/>
								)}
							</div>
						))}
			</div>
		</Popup>
	);
};

export default Settings;
