import './Popup.scss';

interface IPopup extends React.HTMLAttributes<HTMLDivElement> {
	children?: any;
	displClose?: boolean;
	onClose?: () => void;
}

const Popup = (i: IPopup) => {
	const attrs = { ...i };
	delete attrs.displClose;
	return (
		<div {...attrs} className={'bg ' + (i.className || '')}>
			<div className="box">
				{i.displClose && (
					<div className="widget" onClick={i.onClose}>
						<img src="/svgs/x.svg" />
					</div>
				)}
				{i.children}
			</div>
		</div>
	);
};

export default Popup;
