import { useEffect, useState } from 'react';
import Header from '../components/Header/Header';
import HomePage from '../components/pages/HomePage/HomePage';

import './Main.scss';
import QuestPage from '../components/pages/QuestPage/QuestPage';
import Settings from '../components/pages/Settings/Settings';
import Diagrams from '../components/pages/Diagrams/Diagrams';

const Main = () => {
	const [page, setPage] = useState(0);
	useEffect(() => {
		const search = new URLSearchParams(window.location.search);
		setPage(parseInt(search.get('page') || '0') || 0);
	}, []);

	const getSymbol = () => {
		return page == 1 ? '+' : page == 2 ? '-' : page == 3 ? '*' : '/';
	};
	const getDisplaySymbol = () => {
		return page == 1 ? '+' : page == 2 ? '-' : page == 3 ? 'x' : '÷';
	};
	const getQuestName = () => {
		return page == 1
			? 'dodawanie'
			: page == 2
			? 'odejmowanie'
			: page == 3
			? 'mnożenie'
			: 'dzielenie';
	};
	return (
		<div className="app">
			<Header
				onSettingsClick={() => setPage(-1)}
				onDiagramsClick={() => setPage(-2)}
			/>
			{page == 0 && <HomePage />}
			{page >= 1 && page <= 4 && (
				<QuestPage
					symbol={getSymbol()}
					page={page}
					questName={getQuestName()}
					display={getDisplaySymbol()}
				/>
			)}
			{page === -1 && <Settings onClose={() => setPage(0)} />}
			{page === -2 && <Diagrams onClose={() => setPage(0)} />}
		</div>
	);
};

export default Main;
